<template>
	<post-list :posts="posts"></post-list>
</template>

<script>
import PostList from './components/PostList.vue'
import axios from 'axios'

export default {
	name: 'App',
	components: {
		PostList
	},

	data() {
		return {
			posts: []
		}
	},

	created() {
		this.fetchPosts()
	},
	methods: {
		async fetchPosts() {
		try{
			const response = await axios.get('/api/posts')
			this.posts = response.data
		} catch(error) {
			console.log(error)
	}
 } 
}
}
</script>
